.infoBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  text-align: center;

  img {
    margin-bottom: 25px;
  }

  h2 {
    font-size: 22px;
    margin-bottom: 10px;
  }

  p {
    padding: 0;
    margin: 0 0 40px 0;
    color: #9B9B9B;
  }
}
