@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;900&display=swap);
body{margin:0;background:#E7F6FF;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{font-family:'Inter', sans-serif}.main-btn{width:32px;height:32px;background:#FFFFFF;border:1px solid #F2F2F2;box-sizing:border-box;border-radius:8px;cursor:pointer;transition:0.2s}.main-btn:hover{border:1px solid #dfdfdf}.action-btn{background:#9DD558;border-radius:18px;color:white;border:none;outline:none;cursor:pointer;font-weight:600}.wrapper{max-width:1080px;width:100%;margin:50px auto;background:#fff;box-shadow:0px 10px 20px rgba(0,0,0,0.04);border-radius:20px}.header{border-bottom:1px solid #EAEAEA}.header__info h3,.header__info p{margin:0}.header__right{padding:0}.header .favorite-link{position:relative}.header .favorites-count{position:absolute;left:50%;bottom:-6.5px;font-size:10px;color:#FF8585;z-index:10;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.content__title{margin:0}.content__cards{display:grid;grid-template-columns:repeat(4, 215px);grid-gap:40px 15px;gap:40px 15px;justify-content:center}.content__search{border:1px solid #F1F1F1;border-radius:10px;box-sizing:border-box;padding:15px 18px}.content__search input{outline:none;border:none;font-size:14px;color:#888}.content__search input::-webkit-input-placeholder{color:#C4C4C4}.content__search input:-ms-input-placeholder{color:#C4C4C4}.content__search input::placeholder{color:#C4C4C4}.drawer__dimmer{position:fixed;top:0;left:0;width:100%;height:100vh;z-index:19;background-color:black;opacity:0.5}.drawer__sidebar{position:fixed;z-index:20;width:100%;max-width:420px;height:100vh;right:0;top:0px;padding:0 30px;background:#FFFFFF;overflow-y:auto;box-shadow:-10px 4px 24px rgba(0,0,0,0.1)}.drawer__sidebar::-webkit-scrollbar{width:4px}.drawer__sidebar::-webkit-scrollbar-track{box-shadow:inset 0 0 5px rgba(0,0,0,0.3);-webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3)}.drawer__sidebar::-webkit-scrollbar-thumb{background-color:darkgrey;outline:1px solid slategrey}.drawer__card{background:#FFFFFF;border:1px solid #F3F3F3;box-sizing:border-box;padding:20px 20px 30px 20px;border-radius:20px;transition:box-shadow 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;transition:box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;transition:box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;margin-bottom:20px}.drawer__card:hover{box-shadow:0px 16px 30px rgba(0,0,0,0.05);-webkit-transform:translateY(-5px);transform:translateY(-5px)}.drawer__text{font-size:14px;margin-left:20px;margin-right:12px}.drawer__btn{padding:8px}.drawer__btn:hover svg path{fill:#8d8d8d}.drawer__order{display:flex;flex-direction:column;width:100%}.drawer__order li{box-sizing:border-box}.drawer__order li div{width:167px;border-bottom:1px dashed #DFDFDF;margin:0 8px}.drawer__actionBtn{position:relative;box-sizing:border-box;padding:18px 40px 18px 80px;transition:0.2s ease-in-out}.drawer__actionBtn img{position:absolute;top:50%;right:40px;-webkit-transform:translateY(-50%);transform:translateY(-50%);transition:inherit}.drawer__actionBtn span{margin-right:80px}.drawer__actionBtn:hover{background-color:#a9da6c}.drawer__actionBtn:hover img{right:30px}.drawer__actionBtn:active{background-color:#91d044}.drawer__actionBtn:disabled{background-color:#bebebe !important;cursor:default !important;-webkit-animation:button-loading 0.7s ease-in-out infinite;animation:button-loading 0.7s ease-in-out infinite}@-webkit-keyframes button-loading{0%{opacity:1}50%{opacity:0.7}100%{opacity:1}}@keyframes button-loading{0%{opacity:1}50%{opacity:0.7}100%{opacity:1}}@media all and (max-width: 992px){.content__cards{grid-template-columns:repeat(2, 215px);grid-gap:40px 40px;gap:40px 40px}}@media all and (max-width: 576px){.wrapper{margin:20px 0}.content__cards{grid-template-columns:repeat(1, 215px);grid-row-gap:40px;row-gap:40px}.content__top{flex-direction:column}.content__top h1{margin-bottom:25px}.header{flex-direction:column;padding:15px 15px 20px 15px !important}.header__left{margin-bottom:20px;flex-direction:column;text-align:center}.header__left img{margin-right:0 !important;margin-bottom:10px;width:60px;height:60px}.drawer__sidebar{padding:15px}.drawer__card{padding:10px 15px}.drawer__card img{width:50px;height:50px}.drawer__order li div{display:none}.drawer__actionBtn{padding:18px 30px 18px 30px}}.orders{display:grid;grid-template-columns:1fr;grid-row-gap:40px;row-gap:40px}

.Info_infoBlock__34Ys-{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;height:100%;text-align:center}.Info_infoBlock__34Ys- img{margin-bottom:25px}.Info_infoBlock__34Ys- h2{font-size:22px;margin-bottom:10px}.Info_infoBlock__34Ys- p{padding:0;margin:0 0 40px 0;color:#9B9B9B}

.follow-btn{position:relative;box-sizing:border-box;padding:18px 45px 18px 65px;transition:0.2s ease-in-out}.follow-btn img{position:absolute;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);left:30px;transition:inherit}.follow-btn span{color:#fff;font-size:14px}.follow-btn:hover{background-color:#a9da6c}.follow-btn:hover img{left:20px}.follow-btn:active{background-color:#91d044}

.card{max-width:215px;width:100%;padding:20px 30px 35px 30px;overflow:hidden;background:#FFFFFF;border:1px solid #F1F1F1;box-sizing:border-box;border-radius:40px;transition:box-shadow 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;transition:box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;transition:box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out}.card:hover{box-shadow:0px 16px 30px rgba(0,0,0,0.05);-webkit-transform:translateY(-5px);transform:translateY(-5px)}.card__favorite{position:absolute;z-index:10}.card p{margin:14px 0;font-size:14px}.card__tag{font-weight:500;font-size:11px;color:#BDBDBD}.card .plus-btn{cursor:pointer}.card__price{font-weight:bold;font-size:14px}.card__btn{padding:10px}.card__btn svg path{transition:0.2s}.card__btn:hover svg path{fill:#8d8d8d}

